
import XHeader from 'vux/src/components/x-header/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import cookie from 'vux/src/tools/cookie/index.js'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Alert from 'vux/src/components/alert/index.vue'
import XTable from 'vux/src/components/x-table/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'


export default {
directives: {
TransferDom
},
components: {
XHeader,
Flexbox,
FlexboxItem,
Cell,
Group,
XButton,
XTable,
Alert
},
data() {
return {
departmentid: this.$store.getters.user.departmentid,
number1: 1,
number2: 1,
userId: cookie.get('userId'),
startTime1: '',
activePace: '',
activeCreatePeople: '',
activeId: '',
active_Context: '',
activeComplete: [],
show: false,
msg: ''
};
},
filters: {
formatDuring: function (value) {
if(value == "" || value == null || value == undefined){
var value="无";
return value
}else {
Date.prototype.toLocaleString = function(){
let months = this.getMonth()+1;
if(months < 10){
months = '0' + months;
}
let dates = this.getDate();
if(dates < 10){
dates = '0' + dates;
}
let hours = this.getHours();
if(hours < 10){
hours = '0' + hours;
}
let minutes = this.getMinutes();
if(minutes < 10){
minutes = '0' + minutes;
}
return this.getFullYear() +'.'+months+'.'+dates+' '+hours+':'+minutes
}
return new Date(value).toLocaleString();


}}
},
methods: {
getActivity() {
this.$http
.get('active/getRunningActive', {
params: {
pageNum: 1,
pageSize: 1
}
})
.then(res => {
console.log(res.data);
this.startTime1 = res.data.list[0].startTime;
this.activePace = res.data.list[0].activePace;
this.activeCreatePeople = res.data.list[0].activeCreatePeople;
this.active_Context = res.data.list[0].active_Context;
this.activeId = res.data.list[0].id;
})
.catch(function(error) {
console.log(error);
});
},
gettimes1() {
this.$http
.post('active/getParticipateCount', {
userId: this.userId,
activeType: 3
})
.then(res => {
console.log(res);
this.number1 = res.data;
})
.catch(function(error) {
console.log(error);
});
},
gettimes2() {
this.$http
.post('active/getParticipateCount', {
userId: this.userId,
activeType: 5
})
.then(res => {
console.log(res);
this.number2 = res.data;
})
.catch(function(error) {
console.log(error);
});
},
submit() {
this.$http
.post('active/participate', {
userId: this.userId,
activeId: this.activeId,
departmentid: this.departmentid
})
.then(res => {
(this.msg = res.msg), (this.show = true);
})
.catch(function(error) {
console.log(error);
});
},
getAlreadyActive() {
this.$http
.get('active/getAlreadyActive', {
params: {
pageNum: 1,
pageSize: 4
}
})
.then(res => {
this.activeComplete = res.data.list;
console.log(this.activeComplete);
})
.catch(function(error) {
console.log(error);
});
},
onHide() {
console.log('on hide');
},
onShow() {
console.log('on show');
}
},
mounted() {
this.getActivity();
this.gettimes1();
this.gettimes2();
this.getAlreadyActive();
}
};



